import Card from 'react-bootstrap/Card';
import React, { useState } from "react";
// import ModalElt from './Modal';
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'



function Projet(props) {
  return (
  <Card className="bg-dark text-black">
    <Card.Img src={props.ElementObj.miniature} alt="Test imgSimon" />
    <Card.ImgOverlay>
      <Card.Title>{props.ElementObj.titre}</Card.Title>
      <ModalElt monCarousel = {props.ElementObj.Carousel}></ModalElt>
    </Card.ImgOverlay>
  </Card> 
  );

          function ModalElt(props) {
          var monCarousel = props.monCarousel;
          const [show, setShow] = useState(false);
          const [index, moveIndex]= useState(0);
          
            return (
              <>
                <Button variant="primary" onClick={() => setShow(true)}>
                  Découvrir
                </Button>
          
                <Modal
                  show={show}
                  onHide={() => setShow(false)}
                  dialogClassName="modal-90w"
                  aria-labelledby="example-custom-modal-styling-title"
                >
                      <Modal.Header closeButton>
                          <Modal.Title id="example-custom-modal-styling-title">
                            <div>Slide {index+1}</div> 
                          </Modal.Title>
                      </Modal.Header>
                      <Modal.Body>
                          <img src={monCarousel[index].img}></img>
                          <p>{monCarousel[index].legende}</p>
                          <Button onClick={ (compteur2)=>{
                                compteur2 = index;
                                if (compteur2 === 0){
                                    moveIndex(monCarousel.length-1);
                                } else {moveIndex(index-1)}   
                                }}
                              >
                                Précédent
                              </Button>  
                          <Button onClick={ (compteur)=>{
                                compteur = index;
                                if (compteur === [monCarousel.length]-1){
                                    moveIndex(0);
                                } else {moveIndex(index+1)}   
                                }}
                              >
                                Suivant
                              </Button>

                      </Modal.Body>
              
                </Modal>
                
              </>
            );
          }

}


export default Projet;
