// import logo from './logo.svg';
import './App.css';
import Projet from './components/Projet';
import PiedPage from './components/Footer';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import DatasArr from './components/DatasArr';

function App() {
  return (
    <div className="App">
    <Container>
        <Row>
          <Col xs={12} md={4} lg={4}>
              <Projet ElementObj = {DatasArr[0]}/>
          </Col>
          <Col xs={12} md={4} lg={4}><Projet ElementObj = {DatasArr[1]}/>
            </Col>
            <Col xs={12} md={4} lg={4}><Projet ElementObj = {DatasArr[2]}/>
          </Col>
        </Row>
        <Row>
          <Col xs={12} md={4} lg={4}><Projet ElementObj = {DatasArr[3]}/></Col>
          <Col xs={12} md={4} lg={4}><Projet ElementObj = {DatasArr[4]}/></Col>
          <Col xs={12} md={4} lg={4}></Col>
        </Row>
        <Row>
        <Col xs={12} md={12}><PiedPage/>
          </Col>
        </Row>
    </Container>
     {/* <ThemeSwitcher/> */}

    </div>
  );
}

export default App;
